import * as React from 'react';

import Dashboard from './dashboard/Dashboard'
import Box from '@mui/material/Box';
import { PinState, PinIOStateType, GPIOTimeSeriesData } from './types';
import { useState } from 'react';

interface GPIOState { [key: string]: Pick<PinState, 'id' | 'updatedAt' | 'io'> }

const initialGPIOState: GPIOState = {};
for (let i = 1; i <= 40; i += 1) {
    initialGPIOState[i.toString()] = {
      id: i.toString(),
      updatedAt: new Date(Date.now() + i * 30 * 60),
      io: PinIOStateType.IN,
    }
}

export default function App() {
  const [data, setData] = useState<GPIOTimeSeriesData>([]);

  return (
    <Box>
      <Dashboard />
    </Box>

  );
}
